@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
.header__container {
  display: flex;
  justify-content: space-between;
}

.header__container__right {
  display: flex;
}

.hamburgerMenu__icon {
  font-size: 22px;
  margin-top: 1rem;
  margin-left: 1rem;
}

.hamburger__menu__content {
  display: flex;
  flex-direction: column;
  background-color: grey;
  height: 100vh;
  width: 80%;
  position: absolute;
  display: none;
}
.hamburger__menu__content a {
  color: #fff;
  text-decoration: none;
  padding-top: 1.5rem;
  text-align: center;
  font-size: 14px;
}
.hamburger__menu__content hr {
  margin-top: 0.5rem;
}

.windowClose__icon {
  margin-left: auto;
  font-size: 24px;
  cursor: pointer;
}

.searchBar__icon,
.user__icon,
.shoppingCart__icon {
  margin-right: 20px;
  font-size: 18px;
  cursor: pointer;
  margin-top: 1rem;
  margin-left: 1rem;
}

.logo img {
  display: block;
  max-width: 15%;
  margin: 1rem auto 0;
  cursor: pointer;
}

@media only screen and (min-width: 892px) {
  .logo img {
    max-width: 5%;
  }
}

.banner__container {
  display: grid;
  grid-template-columns: 1fr;
}

.banner__container img {
  max-width: 100%;
  height: auto;
  cursor: pointer;
  margin: 1rem auto 0;
}

.banner__container img:hover {
  opacity: 0.6;
}

@media only screen and (min-width: 892px) {
  .banner__container {
    grid-template-columns: 1fr 1fr;
  }
  .banner__container {
    padding: 0 5rem;
  }
}

.sales__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.sales__container img {
  max-width: 100%;
  height: auto;
}

.sales__container a {
  text-decoration: none;
  color: #000000;
  text-align: center;
}

.best__seller__price {
  color: #941313;
  font-weight: bold;
  text-align: center;
}

@media only screen and (min-width: 892px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (min-width: 1200px) {
  .sales__container {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}

.footer__lists {
  display: flex;
  justify-content: space-around;
}

.footer__lists h3 {
  font-weight: lighter;
}

.footer__lists a {
  text-decoration: none;
  color: #ccc;
  font-size: 12px;
}

.footer__lists a:hover {
  color: #fea500;
}

@media only screen and (max-width: 700px) {
  .footer__container {
    background: #222222;
    color: #ffffff;
    margin-top: 4rem;
  }
  .footer__lists {
    display: flex;
    flex-direction: column;
  }
  .company__info,
  .customer__service,
  .my__account,
  .contact__us,
  .footer__copyright {
    margin: 2rem 0 0 2rem;
  }
  .footer__lists hr {
    width: 95%;
    margin-top: 0.5rem;
  }

  .footer__lists h3 {
    font-size: 16px;
  }
  .footer__lists ul {
    margin-top: 1rem;
  }

  .footer__lists a {
    font-size: 14px;
  }
  .gmail__hr {
    margin-top: 2rem;
  }

  .footer__copyright__left {
    font-size: 15px;
  }
  .footer__copyright__left span {
    color: #fea500;
    font-weight: bold;
  }

  .footer__copyright__right img {
    max-width: 100%;
    height: auto;
    margin: 3rem 0 1rem 5rem;
  }
}

.shirtSales__container {
  margin-top: 2rem;
}

.mainShirt__front {
  max-width: 100%;
  height: auto;
  margin-top: 2rem;
}

.mainShirt__small {
  margin-left: 1rem;
}

.mainShirt__small img {
  margin: 2rem 0.5rem;
  border: 1px solid #808080;
  cursor: pointer;
}

.shirtSales__container h1 {
  color: #444444;
  font-weight: bold;
  font-size: 120.57%;
  margin: 15px 0 10px 15px;
}

.shirtSales__price__container {
  display: flex;
  margin-top: 1rem;
  margin-left: 1rem;
}

.shirtSales__price {
  font-size: 2em;
  color: #941212;
  font-weight: normal;
  line-height: 28px;
}

.price__hr {
  margin-top: 1rem;
}

.in__stock {
  margin-left: 2rem;
  border: 1px solid #ddd;
  width: 70px;
  height: 30px;
  text-align: center;
  line-height: 28px;
}

.user__star {
  color: #fea500;
  margin-top: 1rem;
  margin-left: 1rem;
}
.user__star__hr {
  margin-top: 1rem;
}

.size__chart {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  margin-left: 1rem;
  margin-top: 1rem;
}

.size__chart a:first-child {
  color: #ff0000;
  font-size: 1.1em;
  text-decoration: underline;
}

.size__chart a:last-child {
  color: #000;
  font-size: 1.1em;
}

.size__chart__hr {
  margin-top: 1rem;
}

.shipping__information {
  margin-top: 1rem;
  padding: 20px;
}

.tshirtSales__additional__parts {
  background-color: #f5f5f5;
  width: 95%;
  height: 300px;
  margin: 3rem auto 0;
  color: #777777;
}
.tshirt__size,
.tshirt__name,
.tshirt__number {
  display: flex;
  flex-direction: column;
  margin: 2rem auto;
}

.tshirt__size select,
.tshirt__name input,
.tshirt__number input {
  width: 240px;
  height: 30px;
  margin-top: 0.4rem;
  text-align: center;
}

.tshirt__size span {
  color: #ff0000;
}

.tshirt__name span,
.tshirt__number span {
  color: #000;
  font-weight: bold;
}

.tshirtSales__additional__parts p {
  color: #ff0000;
  margin-top: 2rem;
}

.shirtSales__container button {
  display: block;
  width: 222px;
  height: 48px;
  background-color: #000;
  color: #fff;
  font-weight: bolder;
  font-size: 18px;
  text-align: center;
  margin-top: 1rem;
  margin-left: 0.5rem;
}

.heart__icon {
  color: #808080;
  width: 36px;
  height: 36px;
  border: 1px solid black;
  text-align: center;
  padding-top: 0.3rem;
  margin-top: 2rem;
  grid-gap: 2.5rem;
  gap: 2.5rem;
  line-height: 28px;
  display: inline-block;
}

.thirt__description {
  width: 95%;
  margin: 2rem auto;
  border: 1px solid #e9e9e9;
}

.thirt__description h1 {
  color: #fea500;
  font-weight: 700;
}

.tshirt__list {
  margin-top: 1rem;
  padding-left: 2rem;
  line-height: 28px;
}

.tshirt__description__text {
  margin-top: 2rem;
  padding: 0 2rem;
  font-weight: bold;
}

.tshirt__description__text span {
  color: #ff0000;
}

.thirt__description img {
  max-width: 100%;
  height: auto;
  margin-top: 2rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: "Source Sans Pro", sans-serif;
}

.about__us__container h1,
.delivery__container h1,
.privacy__container h1,
.terms__container h1 {
  font-size: 24px;
  font-weight: bold;
  color: #444444;
  text-align: center;
  margin-top: 2rem;
}

.about__us__text,
.delivery__text,
.privacy__text,
.terms__text {
  border: 1px dotted #444444;
  margin-top: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.about__us__text p,
.delivery__text p,
.privacy__text p,
.terms__text p {
  padding: 20px;
  font-size: 16px;
}

.size__chart__container h1 {
  font-size: 24px;
  font-weight: bold;
  color: #444444;
  text-align: center;
  margin-top: 2rem;
}

.men__jersey,
.women__jersey {
  border: 1px solid black;
}

